import React, { useEffect } from "react";
import { Container, AppBar, Toolbar } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { shipmentSelector } from "../../slices/shipmentSlice";

import ShipmentOrders from "./ShipmentOrders";
import {
  GetNextShipmentOrders,
  GetShipmentOrders,
} from "../../services/shipmentService";

import InfiniteScroll from "react-infinite-scroll-component";

import EndMessage from "./EndMessage";
import Loading from "./Loading";

function Shipment() {
  const dispatch = useDispatch();
  const { orders, pageNum, totalPage } = useSelector(shipmentSelector);

  const fetchData = () => {
    dispatch(GetNextShipmentOrders({ pageNum: pageNum + 1 }));
  };
  useEffect(() => {
    dispatch(GetShipmentOrders({ pageNum }));
  }, []);

  return (
    <>
      <AppBar color="default" elevation={0}>
        <Toolbar>Header</Toolbar>
      </AppBar>
      <Toolbar />
      <InfiniteScroll
        dataLength={orders.length}
        next={fetchData}
        hasMore={pageNum !== totalPage}
        loader={<Loading />}
        endMessage={<EndMessage />}
      >
        <ShipmentOrders data={orders} />
      </InfiniteScroll>
    </>
  );
}

export default Shipment;
