import request from "../helpers/request";
import { createAsyncThunk } from "@reduxjs/toolkit";
import { setUser, setToken } from "../helpers/storage";
import { logout } from "../helpers/authentication";

export const LoginUser = createAsyncThunk(
  "user/login",
  async (user, thunkAPI) => {
    try {
      const response = await request.post("user/login", user);
      const { data, code, msg } = response;
      // ,token
      if (code === 0) {
        setToken(data.accessToken);
        setUser(data);
        return { ...data, isLogin: true };
      }
      // ( Error)
      return { ...response, isLogin: false, errorMessage: msg };
    } catch (error) {
      return thunkAPI.rejectWithValue(error);
    }
  }
);

// 
export const LogoutUser = createAsyncThunk(
  "user/logout",
  async (_, thunkAPI) => {
    try {
      const response = await request.post("user/logout");
      const { data, code, msg } = response;
      if (code === 0) {
        logout();
        return { ...response, isLogin: false, errorMessage: msg };
      }
      return response;
    } catch (error) {
      return thunkAPI.rejectWithValue(error);
    }
  }
);
