import React from "react";
import { Helmet } from "react-helmet";

import { BrowserRouter, Routes, Route } from "react-router-dom";
import { useDispatch } from "react-redux";
import { CssBaseline } from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import theme from "./theme";
import { initialize } from "./slices/userSlice";

import Home from "./screens/Home";
import Login from "./screens/Login";
import NotFound from "./screens/NotFound";
import Admin from "./screens/Admin";
import Profile from "./screens/Profile";
import Shipment from "./screens/Shipment";
import ShipmentDetail from "./screens/ShipmentDetail";

function App() {
  const dispatch = useDispatch();
  // 
  dispatch(initialize());

  return (
    <ThemeProvider theme={createTheme(theme)}>
      <Helmet>
        <title> {process.env.REACT_APP_PAGE_TITLE} </title>
      </Helmet>
      <CssBaseline />
      <BrowserRouter>
        <Routes>
          {/* Admin(auth) */}
          <Route path="/" element={<Admin />}>
            <Route path="/" element={<Home />}>
              <Route index element={<Shipment />} />
              <Route path="/profile" element={<Profile />} />
            </Route>
            <Route path="/shipment/:id" element={<ShipmentDetail />} />
            <Route path="/profile/a" element={<div>dadad</div>} />
          </Route>
          {/* Login */}
          <Route path="/login" element={<Login />} />
          <Route path="*" element={<NotFound />} />
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
