import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
import { AppBar, Container, Toolbar } from "@mui/material";
import { List, ListItem, ListItemButton, ListItemText } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { GetShipmentOrderDetails } from "../../services/shipmentService";
import { shipmentSelector } from "../../slices/shipmentSlice";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
function ShipmentDetail() {
  const { id } = useParams();
  const { details } = useSelector(shipmentSelector);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(GetShipmentOrderDetails({ id: id }));
  }, [id]);

  return (
    <>
      <AppBar color="default" elevation={0}>
        <Toolbar>Header</Toolbar>
      </AppBar>
      <Toolbar />
      {details && (
        <List>
          <ListItem disableGutters>
            <ListItemButton>
              <ListItemText>sda</ListItemText>
              <ListItemText sx={{ textAlign: "right" }}>
                sds
              </ListItemText>
              <ChevronRightIcon />
            </ListItemButton>
          </ListItem>
        </List>
      )}
    </>
  );
}

export default ShipmentDetail;
