import { createSlice } from "@reduxjs/toolkit";
import {
  GetShipmentOrders,
  GetNextShipmentOrders,
  GetShipmentOrderDetails,
} from "../services/shipmentService";

export const shipmentSlice = createSlice({
  name: "shipment",
  initialState: {
    total: 0,
    pageNum: 1,
    totalPage: 0,
    status: [],
    orders: [],
    details: null,
  },
  reducers: {},
  extraReducers: {
    [GetShipmentOrders.fulfilled]: (state, { payload }) => {
      state.total = payload.total;
      state.totalPage = payload.totalPage;
      state.orders = payload.data || [];
    },
    [GetNextShipmentOrders.fulfilled]: (state, { payload }) => {
      ++state.pageNum;
      state.totalPage = payload.totalPage;
      state.orders = state.orders.concat(payload.data);
    },
    [GetShipmentOrderDetails.fulfilled]: (state, { payload }) => {
      state.details = payload.data;
    },
  },
});
export const {} = shipmentSlice.actions;
export const shipmentSelector = (state) => state.shipment;
export default shipmentSlice.reducer;
