import request from "../helpers/request";
import { createAsyncThunk } from "@reduxjs/toolkit";

export const GetShipmentOrders = createAsyncThunk(
  "shipment_order/search",
  async (options) => {
    return await request.post("shipment_order/search", options);
  }
);

export const GetNextShipmentOrders = createAsyncThunk(
  "next_shipment_order/search",
  async (options) => {
    return await request.post("shipment_order/search", options);
  }
);

export const GetShipmentOrderDetails = createAsyncThunk(
  "shipment_order/detail",
  async (options) => {
    return await request.post("shipment_order/detail", options);
  }
);
