import React from "react";
import {
  List,
  ListItem,
  ListItemButton,
  ListItemAvatar,
  Avatar,
  ListItemText,
} from "@mui/material";
import ImageIcon from "@mui/icons-material/Image";
import { Link } from "react-router-dom";

function ShipmentOrders({ data }) {
  return (
    <List>
      {data.map((item, index) => (
        <ListItem key={item.id} disableGutters>
          <ListItemButton component={Link} to={`/shipment/${item.id}`}>
            <ListItemAvatar>
              <Avatar>
                <ImageIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={item.orderNumber} secondary="Jan 9, 2014" />
          </ListItemButton>
        </ListItem>
      ))}
    </List>
  );
}

export default ShipmentOrders;
